import React from 'react';
import { connect } from 'react-redux'
import { randomizeFace, saveImage } from '../../reducers/preview'
import './button-actions.scss'

function ButtonActions(props) {
  console.log(props.setName);
  return (
    <div className="ButtonActions">
      <button className="btn btn-primary btn-wide mb-2" onClick={props.randomizeFace}>
        Random
      </button>
      <br/>
      <button className="btn btn-primary btn-wide" onClick={() => props.saveImage(props.setName)}>
        Download
      </button>
    </div>
  )
}



export default connect(
  null,
  dispatch => ({
    randomizeFace: () => dispatch(randomizeFace()),
    saveImage: (setName) => dispatch(saveImage(setName))
  })
)(ButtonActions)
